nav{
  padding-top:20px;
  min-height: 10vh;
  display:flex;
  justify-content: space-around;
  align-items: center;
  button{
    height: 20%;
    background: transparent;
    border:none;
    border: 2px solid rgb(65, 65, 65);
    cursor:pointer;
    padding: 0.5rem;
    transition: all 0.3s ease;
    &:hover{
      background: rgb(65, 65, 65);
      color: white;
    }
  }
}

@media screen and (max-width: 768px){

  nav{
    button{
      z-index:10;
    }
  }
}