.player{
  min-height: 20vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
}

.time-control{
  
  width: 50%;
  display: flex;

  input{
    width: 100%;
    padding: 1rem 0rem;
    cursor: pointer;
    -webkit-appearance: none;
    background: transparent;
   
  }
  p{
    padding: 1rem;
  }
 
}

.play-control{
  width: 30%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  pad: 1rem;
  svg{
    cursor: pointer;
  }
}
.play,
.skip-forward,
.skip-back{
  cursor: pointer;
}

input[type="range"]:focus{
  outline: none;
}

input[type="range"]::-webkit-slider-runnable-track{
  -webkit-appearance: none;
  height: 16px;
  width: 16px;
  
  
}
input[type="range"]::-moz-range-thumb{
  -webkit-appearance: none;
  background: transparent;
  border: none;
}

.track{
  background: lightblue;
  width: 100%;
  height: 1rem;
  
  position: relative;
  overflow: hidden;
  border-radius: 1rem;

}

.animate-track{
  background: lightgray;
  width: 100%;
  height: 100%;
 
  position: absolute;
  top: 0;
  left: 0;
  transform: translateX(0%);
  pointer-events: none;
}

@media screen and (max-width: 768px){

  .time-control{
    width: 90%;
  }
  .time-control{
    width:80%
  }
}